.navbar {/*se le da tamaño y posicion al header*/
  width: 100%;
  height: 100px;
  background-color: rgb(14, 139, 139);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.LogoInicio {
  display: flex;
  width: 200px;
  height: 100%

}

.LogoInicio img{
  height: 100%;
  width: 100%;
}

.links {/*se le da posicion a los contenedores de los link */
  display: flex;
  align-items: center;
  margin-right: 40px;
}



.links a {/*se le agrega estilo a los textos de los links*/
  text-decoration: none;
  color: white;
  margin-left: 20px;
  font-size: 25px;
}

.carrito {
    width: 60px;
    height: 60px;
}


h1{
  border: 6px solid black;
}

h1:hover{
  cursor: default;
  color: rgb(0, 0, 0);
  animation: girar 1s  linear;
}


@keyframes girar {
  0% {
    transform: rotate(0deg); /* Estado inicial: sin giro */
  }
  
  100% {
    transform: rotate(360deg)}
   } /* Estado final: giro completo */
   /*El carro se mueve de izquierda a derecha cuando se le pasa por encima el mouse*/


   @keyframes timbrar {
    0% {
      transform: translateX(0px); /* Estado inicial: sin giro */
    }
    10% {
      transform: translateX(10px); /* Estado inicial: sin giro */
    }
    20% {
      transform: translateX(20px); /* Estado inicial: sin giro */

    }
    30% {
      transform: translateX(30px); /* Estado inicial: sin giro */
      
    }
    40% {
      transform: translateX(40px); /* Estado inicial: sin giro */ 
    }
    50% {
      transform: translateX(50px); /* Estado inicial: sin giro */
    }
    60% {
      transform: translateX(60px); /* Estado inicial: sin giro */
    }
    70% {
      transform: translateX(70px); /* Estado inicial: sin giro */ 
    }
    80% {
      transform: translateX(80px); /* Estado inicial: sin giro */
    }
    90% {
      transform: translateX(90px); /* Estado inicial: sin giro */
    }
    100% {
      transform: translateX(100px); /* Estado inicial: sin giro */
    }
  
     } /* Estado final: giro completo */